@layer theme, base, components, utilities;
@import "tailwindcss/theme.css" layer(theme);
@custom-variant dark (&:where(.dark, .dark-mode-playstore *));
.wp-block-create-block-block-header {
	@import "tailwindcss/preflight.css" layer(base);
	@import "tailwindcss/utilities.css" layer(utilities);

	/* navigation/14/bold */
	font-family: Urbanist;
	font-size: 14px;
	font-style: normal;
	font-weight: 700;
	letter-spacing: 2.1px;
	text-transform: uppercase;

	@layer components {
		.playstore-header {
			@apply w-full bg-[#ffffff] dark:bg-[#0E0D0F] border-b border-[#fafafa] py-2 px-8 z-50;

			a {
				@apply text-(--text-primary) whitespace-nowrap;
			}
			button {
				@apply cursor-pointer;
			}

			nav {
				@apply flex items-center justify-between;

				.playstore-nav-links {
					@apply flex items-center;

					.menu-item-has-children {
						@apply flex items-center;

						.sub-menu.visible {
							@apply flex items-center gap-[32px] absolute top-[80px] h-[52px] left-0 right-0 bg-(--secondary-48) px-[268px] py-[16px] z-50;
						}
						.sub-menu {
							@apply hidden;
						}
					}
				}
			}
		}

		.hamburger-overlay {
			background-image: url("../../src/block-header/assets/images/light.png");
			@apply fixed inset-0 z-50 bg-black/20 bg-cover bg-center gap-[48px] flex flex-col px-[16px] py-[0];

			.hamburger-header,
			.hamburger-menu {
				@apply flex justify-between items-center px-8 py-4;
			}

			.hamburger-menu {
				@apply text-(--text-primary) text-[20px];

				.hamburger-nav-links {
					@apply gap-[48px] flex flex-col;

					.menu-item-has-children {
						@apply inline justify-between items-center;

						.sub-menu.visible {
							@apply flex flex-col gap-[16px] px-[16px];
						}

						.sub-menu {
							@apply hidden;
						}
					}
				}
			}
		}
	}
}
